.button{width:100%;height:100%;display:flex;padding:25px 120px 25px 35px;gap:12px;user-select:text;position:relative;overflow:hidden;cursor:pointer}.card{box-shadow:rgba(0,0,0,.2) 0px 3px 3px -2px,rgba(0,0,0,.14) 0px 3px 4px 0px,rgba(0,0,0,.12) 0px 1px 8px 0px;border-radius:25px;overflow:hidden}.img{width:80px;height:80px;border-radius:16px}.button::before{content:"Подробнее";display:flex;align-items:center;justify-content:center;position:absolute;top:0;right:0;width:100px;height:100%;background:#6d71f9;color:#fff;transition:.5s;opacity:0;font-family:"Inter",sans-serif}@media(max-width: 1150px){.button::before{display:none}.button{padding:25px 20px 25px 20px}}.button:hover::before{opacity:1}